.Section_head h1 {
  font-size: 40px;
  text-align: center;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
}

.Section_head p {
  font-size: 20px;
  text-align: center;
  width: 590px;
  margin: 20px auto;
  font-family: "Poppins", sans-serif;
}

.Product_content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.ProductCard {
  width: 25%;
  margin-bottom: 50px;
}

.ProductCard img {
  margin-bottom: 20px;
}

.ProductCard h2 {
  font-size: 16px;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  width: 240px;
  line-height: 20px;
  margin-bottom: 10px;
}

.ProductCard ul {
  display: flex;
}

.ProductCard ul li h6 {
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  margin-right: 20px;
  margin-bottom: 10px;
}

.CrossedDecoration {
  text-decoration: line-through;
  color: gray;
}

.Buttons {
  color: white;
}

.Buttons ul {
  display: flex;
}

.Buttons ul li {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #828bb2;
  max-width: 30px;
  max-height: 30px;
  padding: 4px;
  margin: 5px;
  border-radius: 50%;
  cursor: pointer;
}
